/* ==================================Независимый контейнер Заголовка======================== */
.main-title {
    display: flex;
    justify-content: center;
    font-size: 55px;
    margin: 10px;
}

/* ==================================Главный контейнер НОВОСТЕЙ и ВИДЖЕТА======================== */
.common-backdrop {
    display: flex;
    justify-content: center;
    background-color: rgb(255, 255, 255);
    width: 100vw;
    /* ==========del&all============ */
}

/* ==================================контейнер Новости======================== */
.news {
    display: flex;
    width: 960px;
    /* flex-flow: row wrap; */
    flex-direction: column;
    justify-content: center;
}

.one-news-container {
    box-sizing: border-box;
    background-color: rgb(255, 255, 255);
    padding: 20px;
    margin: 10px;
    border-radius: 10px;
    border: 1px solid black;
    
}

.author-text {
    font-size: 18px;
    font-weight: bold;
    border-bottom: 2px solid #000;
}

.title-text {
    font-size: 16px;
    font-weight: bold;
    border-bottom: 1px solid #000;
}

/* ==================================Контейнер Виджитов======================== */
.author {
    position: sticky;
    top: 15px;
    background-color: rgb(252, 250, 248);
    height: 90vh;
    width: 200px;
    border-radius: 10px;
    border: 1px solid black;
    margin-top: 10px;


    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.authors-of-articles {
    font-size: 20px !important;
    color: rgb(240, 10, 10);
    font-weight: bold;
    border-bottom: 0px solid black !important;
}

.author p {
    width: 150px;
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    border-bottom: 1px solid black;
    
}
.link{
    color: #000 !important;
}

.loading {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 4px solid rgb(98, 98, 221);
    height: 100vh;
    box-sizing: border-box;
    
}